import { Component, OnInit } from '@angular/core';
import { LeaveHistoryService } from '../leave-history.service';
import { Leavehistory } from '../leavehistory';
@Component({
  selector: 'app-leave-history',
  templateUrl: './leave-history.component.html',
  styleUrls: ['./leave-history.component.scss']
})
export class LeaveHistoryComponent implements OnInit {

  leaveHistory: Leavehistory[];

  constructor(private leavehistoryService: LeaveHistoryService) { }

  ngOnInit(): void {
    this.leavehistoryService.getAllLeaveHistory().subscribe(
      (Response: Leavehistory[]) => {
        this.leaveHistory = Response;
      }
    );
  }

}
